:root{--scroll-origin: 0;--finger-highlight-origin: 0;--theme-color: #000000;--safe-area-top: env(safe-area-inset-top);--safe-area-right: env(safe-area-inset-right);--safe-area-bottom: env(safe-area-inset-bottom);--safe-area-left: env(safe-area-inset-left)}html,body{position:fixed;width:100vw;height:100vh;margin:0;padding:0;background-color:#000000;background-color:var(--theme-color);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}button{padding:10px;border-radius:5px;background-color:#0084ff;color:#fff;font-size:20px;margin:10px;border:0;outline:0;font-size:large}button:active{background-color:#1963a9}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

#splash{position:absolute;background-color:black;display:flex;z-index:100200;transform:translateZ(200px);justify-content:center;align-items:center;top:0px;left:0px;width:100vw;height:100vh;transition:opacity 1s ease 2s}#splash svg#logo_anime{width:100vw;height:100vh;margin:0;position:absolute;top:0px;left:0px}#splash svg#logo_anime>g{fill:url(#logo_grad)}#splash.clear{opacity:0}

.window-list{position:absolute;perspective:300px;perspective-origin:50vw 0;width:100vw;height:100vh;transform-style:preserve-3d;z-index:6;transition:0.5s;visibility:visible;opacity:1}.window-list.away{transform:translateY(-100vh);visibility:hidden;opacity:0}.window-list.scrolling>.app-window{transition-duration:0s}.app-window{position:absolute;bottom:0;left:0;width:100vw;height:100vh;background-color:white;transform-origin:50vw 0vh;transition-duration:0.2s;z-index:7}.app-window.focus{transform:none;opacity:1}.app-window.focus>.list-cover{display:none}.app-window.away{transform:translateZ(-100px);opacity:0}.app-window>.list-cover{position:absolute;z-index:20;width:100%;height:100%}.window-content{position:absolute;left:0;right:0;top:0;bottom:0;margin:auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;text-align:center}

